





















import { Component, Ref,Vue,Prop, Watch } from "vue-property-decorator";

@Component
export default class AudioComponent extends Vue {
 	@Prop() toolbarInfo?: {};
    @Ref("leftAudio") readonly leftAudio!:HTMLAudioElement;
    @Ref("rightAudio") readonly rightAudio!:HTMLAudioElement;
    @Ref("doubleAudio") readonly doubleAudio!:HTMLAudioElement;

    currentTime: number = 0;
    currentAudio: string = "double";
	volume:number = 50;
	playbackRate:number = 1;

	@Watch("volume")
	playbackRateChange(){
	  this.playbackRate = this.volume / 50;
	   switch(this.currentAudio){
            case "left":
			   this.leftAudio.playbackRate = this.playbackRate;
               break;
            case "right":
			   this.rightAudio.playbackRate = this.playbackRate;
                break;
            case "double":
				this.doubleAudio.playbackRate = this.playbackRate;
                break;
        }
	}

	
    play(audioName: string){
        if(audioName == undefined){
            audioName = "double";
		}
		this.stop();
        switch(this.currentAudio){
            case "left":
               this.currentTime = this.leftAudio.currentTime;
               break;
            case "right":
               this.currentTime = this.rightAudio.currentTime;
                break;
            case "double":
                this.currentTime = this.doubleAudio.currentTime;
                break;
        }
    
        switch(audioName){
            case "left":
				this.leftAudio.currentTime = this.currentTime;
				this.leftAudio.playbackRate = this.playbackRate;
                this.leftAudio.play()
                break;
            case "right":
				this.rightAudio.currentTime = this.currentTime;
				this.rightAudio.playbackRate = this.playbackRate;
                this.rightAudio.play();
                break;
            case "double":
				this.doubleAudio.playbackRate = this.playbackRate;
                this.doubleAudio.currentTime = this.currentTime;
                this.doubleAudio.play();
                break;
        }
        this.currentAudio = audioName;
        
	}
	stop(){
		 this.leftAudio.pause();
		 this.doubleAudio.pause();
		 this.rightAudio.pause();
	}

}
